// API Integration Layer for PHP Backend
const BASE_URL = process.env.NEXT_PUBLIC_PHP_BACKEND_URL || "http://localhost/yetram";

// Helper function to handle form data
const createFormData = (data: Record<string, any>): string => {
  const formData = new URLSearchParams();
  Object.keys(data).forEach(key => {
    if (data[key] !== undefined && data[key] !== null) {
      formData.append(key, data[key].toString());
    }
  });
  return formData.toString();
};

// Helper function to handle API responses
const handleResponse = async (response: Response) => {
  const text = await response.text();
  
  // Try to parse as JSON first
  try {
    return JSON.parse(text);
  } catch {
    // If not JSON, return as text
    return text;
  }
};

// User Authentication APIs
export const loginUser = async (credentials: { email: string; password: string }) => {
  try {
    const response = await fetch(`${BASE_URL}/user_login.php`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
      },
      body: createFormData(credentials),
      mode: 'cors',
    });
    
    return await handleResponse(response);
  } catch (error) {
    console.error('Error logging in user:', error);
    throw error;
  }
};

