'use client'

import { useState } from 'react'
import { useRouter } from 'next/navigation'
import Link from 'next/link'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { loginUser } from '@/lib/api'

export default function LoginPage() {
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState('')
  const router = useRouter()

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    setError('')

    try {
      const response = await loginUser({ email, password })
      
      // Check if login was successful (you may need to adjust this based on your PHP response)
      if (typeof response === 'string' && response.includes('Login successful')) {
        // Store user session
        localStorage.setItem('user', email)
        localStorage.setItem('isLoggedIn', 'true')
        
        // Redirect to dashboard
        router.push('/dashboard')
      } else {
        setError('Invalid email or password')
      }
    } catch (err) {
      setError('Login failed. Please try again.')
      console.error('Login error:', err)
    } finally {
      setLoading(false)
    }
  }

  return (
    <div 
      className="min-h-screen flex items-center justify-center bg-cover bg-center"
      style={{
        backgroundImage: 'url(https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/bf2a1185-2778-4792-9bee-26d92d689382.png)',
      }}
    >
      <div className="absolute inset-0 bg-black bg-opacity-50" />
      
      <Card className="relative z-10 w-full max-w-md mx-4 bg-white/20 backdrop-blur-lg border-white/40">
        <CardHeader className="text-center">
          <CardTitle className="text-2xl font-bold text-white">Login</CardTitle>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit} className="space-y-4">
            {error && (
              <div className="bg-red-500/20 border border-red-500/50 text-red-100 px-4 py-2 rounded">
                {error}
              </div>
            )}
            
            <div>
              <Input
                type="email"
                placeholder="Email Address"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
                className="bg-white/30 border-white/50 text-white placeholder:text-white/70"
              />
            </div>
            
            <div>
              <Input
                type="password"
                placeholder="Password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
                className="bg-white/30 border-white/50 text-white placeholder:text-white/70"
              />
            </div>
            
            <Button 
              type="submit" 
              className="w-full bg-blue-600 hover:bg-blue-700 text-white"
              disabled={loading}
            >
              {loading ? 'Logging in...' : 'Login'}
            </Button>
          </form>
          
          <div className="mt-6 text-center">
            <p className="text-white/80">
              Don't have an account?{' '}
              <Link href="/signup" className="text-blue-300 hover:text-blue-200 underline">
                Sign Up
              </Link>
            </p>
            <p className="mt-2">
              <Link href="/admin/login" className="text-white/80 hover:text-white underline">
                Login as Admin
              </Link>
            </p>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
